:root {
    --jumbotron-padding-y: 3rem;
  }
  
  .jumbotron {
    position: relative;
    display: block;
    width: 100%;
    max-width: 1280px;
    padding-top: var(--jumbotron-padding-y);
    padding-bottom: var(--jumbotron-padding-y);
    margin: 0 auto;
    background-color: black;
/*  
    @media (min-width: 767px) {
      .jumbotron {
        padding-top: calc(var(--jumbotron-padding-y) * 2);
        padding-bottom: calc(var(--jumbotron-padding-y) * 2);
      } // .jumbotron
    } // min-width: 767px
*/
    .jubmotron-background {
      position: absolute;
      display: block;
      top: 0px;
      left: 0px;
      width: 100%;
      max-width: 1280px;
      height: 100%;
      margin: 0 0 0 0;
      padding: 0 0 0 0;
      z-index: 1;

      video {
        display: inline-block;
        width: 100%;
        max-width: 1280px;
      }

      @media (max-width: 767px) {
        video {
          width: 100%;
          max-width: 767px;

          source {
            width: 100%;
            max-width: 767px;
          }

          img {
            width: 100%;
            max-width: 767px;
          }
  
        } // video
      } // max-width: 767px

    } // .jubmotron-background

    .container {
      position: relative;
      width: 100%;
      max-width: 1280px;
      margin: 0 0 0 0;
      padding: 58px 5%;
      text-align: center;
      z-index: 2;
    }  

    @media (max-width: 767px) {
      .container {
        width: 100%;
        max-width: 767px;
        margin: 0;
        padding: .5rem 0;

      } // .container
    } // max-width: 767px

    .jumbotron-login {
      position: absolute;
      display: inline-block;
      top: -14px;
      right: 44px;
      font-size: 0.875rem;
      text-align: right;
      text-decoration: none;
      color: #fefefe;
    }

    @media (max-width: 767px) {
      .jumbotron-login {
        top: -24px;
        right: 24px;
      } // .jumbotron-login
    } // max-width: 767px

    .jumbotron-heading {
      font-size: 1.875rem;
      font-weight: 600;
      letter-spacing: -0.019rem;
      line-height: 3.000rem;
      color: white;

      em {
        font-style: normal;
        color: $primary;      
      }
    } //.jumbotron-heading

    @media (max-width: 767px) {
      .jumbotron-heading {
        font-size: 1.250rem;
        letter-spacing: -0.013rem;
      } // .jumbotron-heading
    } // max-width: 767px
    
    .jumbotron-logo {
      margin-bottom: 3rem;

      img {
        width: 392px;  
        height: 76px;
      }

      @media (max-width: 767px) {
        img {
          width: 214px;  
          height: 44px;
        } // img
      } // max-width: 767px
      
    } // .jumbotron-logo

    @media (max-width: 767px) {
      .jumbotron-logo {
        margin-bottom: 1.5rem;
      } // .jumbotron-logo
    } // max-width: 767px

    .btn {
      margin-top: 1.875rem;
      font-size: 1.125rem;

      //  *** need to have the bottom right corner cut off
    }

    @media (max-width: 767px) {
      .btn {
        font-size: 1.125rem;
        line-height: 2.750rem;
      } // .btn
    } // max-width: 767px

    p {
      margin: 0 0 0 0;
      padding: 0 0 0 0;
      color: white;
      
      &.lead {
        font-size: 2.500rem;
        letter-spacing: -0.025rem;
        line-height: 3.000rem;
      } //.lead

      @media (max-width: 767px) {
        &.lead {
          font-size: 1.500rem;
          line-height: 1.750rem;
        } // &.lead
      } // max-width: 767px

      &.below-cta {
        font-size: 1.250rem;

        a {
          color: white;
        }
      } 

      @media (max-width: 767px) {
        &.below-cta {
          font-size: 0.750rem;
          line-height: 1.125rem;
        } // &.below-cta
      } // max-width: 767px
    } // p

  } // .jumbotron